.video-section {
  position: relative;
  
  &__image {
    height: 100%;
    width: 100%;
    object-fit: cover;  
    filter: grayscale(1);
    position: absolute;
  }
  
  &:after {
    position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: $midnight;
    opacity: .9;
  }
  
  &__inner {
    @mixin container;
    padding-top: 40px;
    padding-bottom: 40px;
    
    @media (min-width: $medium-breakpoint) {
      padding-top: 70px;
      padding-bottom: 70px;
    }
  }
  
  &__text {
    font-family: $ptsans;
    font-size: 13px;
    line-height: 17px;
    color: #969BA0;
    text-align: center;
    position: relative;
    z-index: 1;
    padding: 0 10px;
    margin-top: 1rem;
    
    @media (min-width: $medium-breakpoint) {
      font-size: 16px;
      line-height: 23px;
    }
    
    @media (min-width: $large-breakpoint) {
      font-size: 19px;
    }
    
    @media (min-width: $large-breakpoint) {
      width: 530px;
      margin-left: auto;
      margin-right: auto;
    }
    
  }
  
}